import activityList from '@/components/activityList.vue';
export default {
	data() {
		return {
			type: 0,
			tabs: [
				{
					name: '未开始',
					list: [],
					pageNum: 1,
					pageSize: 10,
					loading: true,
					more: false
				},
				{
					name: '已完成',
					list: [],
					pageNum: 1,
					pageSize: 10,
					loading: true,
					more: false
				}
			]
		};
	},
	components: { activityList },
	
	onLoad() {
		this.getList();
	},
	onReachBottom() {
		this.getList();
	},
	methods: {
		onChange(e) {
			this.type = e.detail.index;
			if (this.tabs[e.detail.index].list.length === 0) this.getList();
		},
		getList() {
			let tabs = this.tabs;
			let type = this.type;
			if (tabs[type].loading) {
				tabs[type].loading = false;
				this.tabs = tabs;
				this.$Fetch({
					url: "activity_list/getData",
					data: {
						type: this.type,
						pageNum: tabs[type].pageNum,
						pageSize: tabs[type].pageSize
					},
					success: res => {
						console.log(res);
						let len = res.lists.length;
						if (len > 0) {
							tabs[type].list = tabs[type].list.concat(res.lists);
							if (len === tabs[type].pageSize) {
								tabs[type].pageNum++;
								tabs[type].loading = true;
								tabs[type].more = true;
							} else {
								tabs[type].more = false;
							}
							this.tabs = tabs;
						}
					}
				});
			}
		}
	}
};
